
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import axios from "axios";
export default {
components: {
XTextarea,
Group,
XButton,
mavonEditor
},
data: function() {
return {
title: "",
content: "",
toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
/* 1.3.5 */
save: true, // eventssave
/* 1.4.2 */
navigation: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true 
}
};
},
methods: {

uploadImage: function(pos, file) {
var app = this;


console.log(pos);
console.log(file);

var params =new FormData();
params.append('smfile',file);

axios.post("https://sm.ms/api/upload", params,{



headers: { 'Content-Type': 'multipart/form-data' }
}).then(function(response) {


app.$refs.md.$img2Url(pos,response.data.data.url);
});
},

addtopic: function() {


var app = this;
this.$http("/topics", {
title: this.title,
content: this.content,
tab: "dev",
accesstoken: this.$store.state.acccesstoken
}).then(function(response) {

if (response && response.data.success) {

app.$router.replace("/topiclist/dev");
}
});
}
}
};



